.project {
    width: 80%;
    /* margin: 10rem; */
    margin-left: 15rem;
    margin-right: 7rem;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .project-reverse {
    width: 80%;
    margin-left: 7rem;
    margin-right: 7rem;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .__img_wrapper {
    width: 20em;
    min-height: 10.5rem;
    max-height: fit-content;
  }
  .__img_wrapper > img {
    width: 50%;
  }
  .__content_wrapper {
    /* width: 40%; */
    margin-left: 2rem;
    margin-bottom: 2rem;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding-top: 1.5rem;
    display: flex;
    flex-direction: column;
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.51);
  }
  .title {
    font-size: 1.5rem;
    margin: 0;
  }
  .description {
    font-size: 1rem;
    width: 60%;
  }
  @media (max-width: 1024px) {
    .project {
      margin-left: 3rem;
      margin-right: 3rem;
    }
    .__content_wrapper {
      width: 50%;
    }
  }
  @media (max-width: 768px) {
    .project {
      margin-left: 4em;
      margin-right: 4em;
      flex-direction: column;
    }
    .__img_wrapper {
      width: 340px;
    }
    .__content_wrapper {
      width: 70%;
    }
  }